<template>
  <div class="index mescroll">
    <div class="search">
      <form action="/">
        <van-search v-model="value" show-action action-text="搜索" @cancel="onSearch" placeholder="搜索活动名" background="#fff" @search="onSearch" @blur="blur" />
      </form>
    </div>
    <div class="banner">
      <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bannerList" :key="index">
          <img :src="$utils.dealImg(item.bimg)" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="index-classify">
      <grid :grid-array="cateList" width="55px" height="55px" font-color="#666" borderRadius="50%" @goRouter="goRouter" />
    </div>
    <div class="mid">
      <div class="dataList" v-if="false">
        <div class="dataItem" v-for="(item,index) in dataList" :key="index" :style="(index + 1)%3!=0?'':'margin-right:0'">
          <div class="flex">
            <!-- <div :class="'icon ' + 'icon' + (index + 1) ">
              <img :src="item.icon" alt="" />
            </div> -->
            <div class="right">
              <div :class="'num ' + 'color' + (index + 1)">{{item.num}}</div>
              <div class="text">{{item.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="mid-content">
        <div class="mid-top">
          <p class="title">推荐活动</p>
          <p class="more" @click="toMore">查看更多</p>
        </div>
        <div class="line"></div>
        <div class="list1">
          <div class="item" v-for="(item,index) in activityList" :key="index" :style="index%2==0?'margin-right:10px':''" @click="goDetail(item)">
            <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
            <div class="content">
              <p class="name">{{item.aname}}</p>
              <div class="flex">
                <div class="left">{{item.asalenum}}人已购买</div>
                <div class="right">{{item.aoutPrice?'￥' + item.aoutPrice.toFixed(2) : ''}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Search, Swipe, SwipeItem } from "vant";
import Grid from "@/components/Grid";
import { getActivityIndex } from "@/api";
export default {
  components: {
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    Grid,
  },
  data() {
    return {
      value: "",
      bannerList: [],
      cateList: [],
      activityList: [],
      dataList: [
        {
          icon: require("../../assets/img/activity/icon3.png"),
          name: "累计爆款",
          num: 0,
        },
        {
          icon: require("../../assets/img/activity/icon5.png"),
          name: "累计入驻商家",
          num: 0,
        },
        {
          icon: require("../../assets/img/activity/icon6.png"),
          name: "累计发放红包",
          num: 0,
        },
        {
          icon: require("../../assets/img/activity/icon4.png"),
          name: "累计参与网友",
          num: 0,
        },
        {
          icon: require("../../assets/img/activity/icon7.png"),
          name: "累计发放金额",
          num: 0,
        },
        {
          icon: require("../../assets/img/activity/icon8.png"),
          name: "累计获得收益",
          num: 0,
        },
        // {
        //   icon: require("../../assets/img/activity/icon3.png"),
        //   name: "收益",
        //   num: 0
        // },
        // {
        //   icon: require("../../assets/img/activity/icon3.png"),
        //   name: "收益",
        //   num: 0
        // }
      ],
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    onSearch() {
      this.$router.push({
        name: "activityClassify",
        query: {
          value: this.value,
        },
      });
    },
    goRouter(item) {
      this.$router.push({
        name: "activityClassify",
        query: {
          cateCode: item.cateCode,
        },
      });
    },
    toMore() {
      this.$router.push({
        name: "activityClassify",
      });
    },
    goDetail(item) {
      this.$router.push({
        name: "shareActivity",
        query: {
          id: item.acode,
        },
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getActivityIndex().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.bannerList = res.data.BannerList;
          this.cateList = res.data.cateList;
          this.cateList.map(item => {
            item.icon = this.$config.http + item.cateImg;
          });
          this.activityList = res.data.activityList;
          this.dataList.map((item, index) => {
            item.num = res.data.dataList[index];
          });
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      });
    },
  },
  mounted() {
    document.title = this.$config.activeTitle;
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.index {
  height: calc(100vh - 50px);
  .banner {
    .van-swipe-item {
      background: #fff;
      img {
        width: 100%;
      }
    }
   .index-classify{
      border-bottom: 1pt solid #efefef;
    }
  } 
  .mid {
    .dataList {
      padding: 10px;
      .dataItem {
        display: inline-block;
        width: calc((100% - 20px) / 3);
        margin: 5px 10px 5px 0;
        background: #fff;
        border-radius: 5px;
        padding: 10px 0 10px 0;
        .flex {
          display: flex;
          justify-content: center;
          .icon {
            margin: 0 10px;
            border-radius: 50%;
            width: 50px;
            height: 50px;
            text-align: center;
            img {
              width: 50px;
              height: 50px;
            }
          }
          .icon1 {
            background: #fdd383;
          }
          .icon2 {
            background: #fdae83;
          }
          .icon3 {
            background: #83c5fd;
          }
          .icon4 {
            background: #f85858;
          }
          .icon5 {
            background: #fb9b1f;
          }
          .icon6 {
            background: #e8611e;
          }

          .right {
            .num {
              font-weight: bold;
              font-size: 18px;
              line-height: 25px;
              text-align: center;
            }
            .text {
              line-height: 30px;
              color: #999;
              font-size: 14px;
            }
            .color1 {
              color: #fdd383;
            }
            .color2 {
              color: #fdae83;
            }
            .color3 {
              color: #83c5fd;
            }
            .color4 {
              color: #f85858;
            }
            .color5 {
              color: #fb9b1f;
            }
            .color6 {
              color: #e8611e;
            }
          }
        }
      }
    }
    .mid-content {
      background: #fff;
      padding: 10px;
      .mid-top {
        display: flex; 
        align-items: center;
        line-height: 30px;
        font-size: 14px;
        .title {
          font-weight: bold;
          flex: 2;
        }
        .more {
          flex: 1;
          text-align: right;
          color: #999;
        }
      }
      .list {
        .item {
          margin: 5px 0 0;
          img {
            width: calc(100% + 2pt);
            border-radius: 10px 10px 0 0;
            display: block;
          }
          .content {
            box-sizing: border-box;
            border: 1pt solid #e2dfe3;
            border-top: 0;
            border-radius: 0 0 10px 10px;
            padding: 10px;
            .name {
              font-size: 15px;
              width: 100%;
              @include ellipsis-more();
            }
            .flex {
              display: flex;
              align-items: center;
              padding: 10px 0;
              .labels {
                flex: 2;
                .label {
                  border: 1pt solid #000;
                  display: inline-block;
                  font-size: 13px;
                  padding: 0 10px;
                  line-height: 20px;
                  border-radius: 10px;
                }
              }
              .text {
                flex: 1;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
      .list1 {
        .item {
          display: inline-block;
          width: calc((100vw - 10px - 20px) / 2);
          margin: 5px 0;
          // border: 1pt solid #e2dfe3;
          // border-radius: 10px;
          img {
            width: calc(100%);
            // height: calc((100vw - 10px - 20px) / 2 / 1.316);
            // border-radius: 10px 10px 0 0;
            display: block;
          }
          .content {
            box-sizing: border-box;
            // border: 1pt solid #e2dfe3;
            border-top: 0;
            // border-radius: 0 0 10px 10px;
            padding: 10px 5px;
            .name {
              font-size: 15px;
              width: 100%;
              @include ellipsis-more();
            }
            .flex {
              display: flex;
              align-items: center;
              padding: 10px 0;
              .left {
                flex: 2;
                font-size: 13px;
                color: #999;
              }
              .right {
                width: 60px;
                text-align: right;
                color: #9e1435;
                font-size: 13px;
              }
            }
          }
        }
      }
    }
  }
}
</style>